@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  /* убирает пробелы, не дает переносить текст на другие строки */
  clip-path: inset(100%);
  /* определяет область отображения текста по шаблону. совместимость средняя */
  clip: rect(0 0 0 0);
  /* определяет область отображения текста по прямоугольнику. совместимость высокая */
  overflow: hidden;
  /* определяет что делать с элементами, которые не вмещаются в блок: показать/скрыть/скролл. можно применять к блокам, флексам и гридам */
}

.button {
  display: flex;
  justify-content: center;
  padding-top: 14px;
  padding-bottom: 12px;
  min-height: 52px;
  text-transform: uppercase;
  background-color: #ffffff;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}